.btn-default{
  .tpTag &, .tpUser &, .tpSearch &{
    border: 1px solid @border-base;
    box-shadow: none;
    color: darken(@border-base, 80%);
    background: none;
    margin-top: 15px;
    &:hover,&:focus{
      background: none;
    }
  }
}
.tpTag,
.tpUser{
  .dropdown-toggle{
    &.btn{
      background: @background-gray;
      border: 1px solid @border-base;
      box-shadow: none;
      &:hover, &:focus, &:active{
        background: @background-gray;
      }
    }
  }
  .btn-group.open .btn.dropdown-toggle{
    .box-shadow(none);
    background: @background-gray;
  }
}

